import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Layout from "./components/includes/Layout";
import Home from "./components/pages/Home";
import Login from "./components/pages/authentications/Login";
import Signup from "./components/pages/authentications/Signup";
import Activate from "./components/pages/authentications/Activate";
import ResetPassword from "./components/pages/authentications/ResetPassword";
import ResetPasswordConfirm from "./components/pages/authentications/ResetPasswordConfirm";
import Facebook from "./components/pages/authentications/Facebook";
import Aboutus from "./components/pages/Aboutus";
import Google from "./components/pages/authentications/Google";
import { Provider } from "react-redux";
import store from "./store";
import "./App.css";

const App = () => (
  <Provider store={store}>
    <Router>
      <Layout>
        <Switch>
          <Route exact path="/" component={Home} />
          <Route exact path="/login" component={Login} />
          <Route exact path="/aboutus" component={Aboutus} />
          <Route exact path="/signup" component={Signup} />
          <Route exact path="/facebook" component={Facebook} />
          <Route exact path="/google" component={Google} />
          <Route exact path="/reset-password" component={ResetPassword} />
          <Route
            exact
            path="/password/reset/confirm/:uid/:token"
            component={ResetPasswordConfirm}
          />
          <Route exact path="/activate/:uid/:token" component={Activate} />
        </Switch>
      </Layout>
    </Router>
  </Provider>
);

export default App;
