import React, { useState } from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { reset_password } from "../../actions/auth";
import formbg from "../../assets/main_templates_bg/tm_bg.png";
import Footer from "../../includes/Footer";

const ResetPassword = ({ reset_password }) => {
  const [requestSent, setrequestSent] = useState(false);
  const [formData, setformData] = useState({
    email: "",
  });
  const { email } = formData;
  const onChange = (e) =>
    setformData({ ...formData, [e.target.name]: e.target.value });
  const onSubmit = (e) => {
    e.preventDefault();
    reset_password(email);
    setrequestSent(true);
  };
  if (requestSent) {
    return <Redirect to="/" />;
  }
  return (
    <>
      <div className="container py-5">
        <div className="row">
          <div className="col col-md-6 col-sm-12 pt-5">
            <img src={formbg} className="img-fluid" alt="Phone logo" />
          </div>
          <div className="col col-md-5">
            <h1 className="text-bold w-100 mb-5 text-center">Şifreni Yenile</h1>

            <form onSubmit={(e) => onSubmit(e)}>
              <div className="form-outline mb-4 text-left">
                <input
                  id="form1Example13"
                  className="form-control"
                  type="email"
                  placeholder="E-posta adresi"
                  name="email"
                  value={email}
                  onChange={(e) => onChange(e)}
                  required
                />
                <label className="form-label" for="form1Example13">
                  E-posta adresi
                </label>
              </div>

              <button type="submit" className="btn btn-danger btn-lg btn-block">
                Gönder
              </button>
            </form>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
};

export default connect(null, { reset_password })(ResetPassword);
