import React, { useState } from "react";
import { Link, Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { FcGoogle } from "react-icons/fc";
import { BsFacebook } from "react-icons/bs";
import { login } from "../../actions/auth";
import Footer from "../../includes/Footer";
import formbg from "../../assets/main_templates_bg/tm_bg.png";
import axios from "axios";

const Login = ({ login, isAuthenticated }) => {
  const [formData, setFormData] = useState({
    email: "",
    password: "",
  });

  const { email, password } = formData;

  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = (e) => {
    e.preventDefault();

    login(email, password);
  };

  const continueWithGoogle = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_API_URL}/auth/o/google-oauth2/?redirect_uri=${process.env.REACT_APP_API_URL}/google`
      );

      window.location.replace(res.data.authorization_url);
    } catch (err) {}
  };

  const continueWithFacebook = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_API_URL}/auth/o/facebook/?redirect_uri=${process.env.REACT_APP_API_URL}/facebook`
      );

      window.location.replace(res.data.authorization_url);
    } catch (err) {}
  };

  if (isAuthenticated) {
    return <Redirect to="/" />;
  }

  return (
    <>
      <div className="container py-5">
        <div className="row">
          <div className="col col-md-6 py-5">
            <img src={formbg} className="img-fluid" alt="Phone logo" />
          </div>
          <div className="col col-md-5">
            <h1 className="text-bold w-100 mb-5 text-center">Giriş Yap</h1>
            <form onSubmit={(e) => onSubmit(e)}>
              <div className="form-outline text-left">
                <input
                  id="form1Example13"
                  className="form-control"
                  type="email"
                  placeholder="E-posta adresi"
                  name="email"
                  value={email}
                  onChange={(e) => onChange(e)}
                  required
                />
                <label className="form-label" for="form1Example13">
                  E-posta adresi
                </label>
              </div>

              <div className="form-outline text-left">
                <input
                  id="form1Example23"
                  className="form-control"
                  type="password"
                  placeholder="Şifre"
                  name="password"
                  value={password}
                  onChange={(e) => onChange(e)}
                  minLength="6"
                  required
                />
                <label className="form-label" for="form1Example23">
                  Şifre
                </label>
              </div>

              <div className="d-flex justify-content-around align-items-center">
                <Link to="/reset-password">
                  <p className="text-muted">şifreni unuttun mu?</p>
                </Link>
              </div>

              <button type="submit" className="btn btn-danger btn-block">
                Giriş
              </button>
            </form>
            <div className="divider d-flex align-items-center my-4">
              <p className="text-center fw-bold mx-3 mb-0 text-muted">OR</p>
            </div>

            <button
              className="btn btn-primary google btn-block"
              onClick={continueWithGoogle}
            >
              <i className="me-4">
                <FcGoogle />
              </i>
              Google ile devam et
            </button>
            <button
              className="btn btn-primary btn-block"
              onClick={continueWithFacebook}
            >
              <i className="me-4">
                <BsFacebook />
              </i>
              Facebook ile devam et
            </button>
            <p className="text-center mx-3 mt-4 text-muted text-2">
              Devam ederek Temiz'in Hizmet Şartlarını ve
              <strong>Gizlilik Politikamızı </strong>
              okuduğunuzu onaylayın
            </p>
            <div className="divider d-flex align-items-center my-4">
              <p className="text-center fw-bold mx-3 mb-0 text-muted">|</p>
            </div>
            <p className="text-center mx-3 mb-0 text-muted">
              Henüz Temiz ile değilmisiniz?
              <Link to="/signup" className="text-dark">
                Üye ol
              </Link>
            </p>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps, { login })(Login);
