import React, { useState, Fragment } from "react";
import logo from "../assets/logo.png";
import { Link, NavLink, Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { logout } from "../actions/auth";
const Navbar = ({ logout, isAuthenticated }) => {
  const [redirect, setRedirect] = useState(false);
  const logout_user = () => {
    logout();
    setRedirect(true);
  };

  const guestLinks = () => (
    <Fragment>
      <NavLink to="/login" className="btn btn-primary ms-4">
        Giriş <i className="fas fa-sign-in-alt ms-2"></i>
      </NavLink>
    </Fragment>
  );

  const authLinks = () => (
    <Fragment>
      <Link to="/login" onClick={logout_user} className="btn btn-primary ms-3">
        <h6>
          ÇIKIŞ <i className="fas fa-sign-in-alt ms-2"></i>{" "}
        </h6>
      </Link>
    </Fragment>
  );

  const [clicked, setclicked] = useState(false);
  const handleClick = () => {
    setclicked(!clicked);
  };
  return (
    <>
      <nav className="navbar navbar-expand-lg">
        <div className="container">
          <Link className="navbar-brand" to="/">
            <img
              src={logo}
              className="img-fluid my-3"
              alt="logo"
              style={{ height: "4rem" }}
            />
          </Link>
          <div
            className="navbar-toggler"
            type="button"
            onClick={handleClick}
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon">
              <i className={clicked ? "fas fa-times" : "fas fa-bars"}></i>
            </span>
          </div>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav mx-auto mb-2 mb-lg-0">
              <li className="nav-item dropdown">
                <a
                  className="nav-link"
                  href="/"
                  id="homeDropdown"
                  role="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  Anasayfa<i className="fas fa-chevron-down fa-xs ms-2"></i>
                </a>
                <ul className="dropdown-menu" aria-labelledby="homeDropdown">
                  <li>
                    <a className="dropdown-item" href="/home">
                      Anasayfa
                    </a>
                  </li>
                  <li>
                    <a className="dropdown-item" href="/services">
                      Hizmetlerimiz
                    </a>
                  </li>
                </ul>
              </li>
              <li className="nav-item dropdown">
                <a
                  className="nav-link"
                  href="/"
                  id="aboutusDropdown"
                  role="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  Hizmetler <i className="fas fa-chevron-down fa-xs ms-2"></i>
                </a>
                <ul className="dropdown-menu" aria-labelledby="aboutusDropdown">
                  <li>
                    <a className="dropdown-item" href="/aboutus">
                      Ev temizliği
                    </a>
                  </li>
                  <li>
                    <a className="dropdown-item" href="/team">
                      Ofis temizliği
                    </a>
                  </li>
                  <li>
                    <a className="dropdown-item" href="/team">
                      Araç temizliği
                    </a>
                  </li>
                  <li>
                    <a className="dropdown-item" href="/team">
                      Dış mekan temizliği
                    </a>
                  </li>
                </ul>
              </li>
              <li className="nav-item dropdown">
                <a
                  className="nav-link"
                  id="pricingDropdown"
                  href="/"
                  role="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  Temizlikçiler{" "}
                  <i className="fas fa-chevron-down fa-xs ms-2"></i>
                </a>
                <ul className="dropdown-menu" aria-labelledby="pricingDropdown">
                  <li>
                    <a className="dropdown-item" href="/price">
                      Normal
                    </a>
                  </li>
                  <li></li>
                  <li>
                    <a className="dropdown-item" href="/price">
                      Special Price
                    </a>
                  </li>
                </ul>
              </li>
              <li className="nav-item">
                <a className="nav-link" href="/aboutus">
                  Hakkımızda
                </a>
              </li>
            </ul>
            {isAuthenticated ? authLinks() : guestLinks()}
          </div>
        </div>
      </nav>
      {redirect ? <Redirect to="/" /> : <Fragment></Fragment>}
    </>
  );
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps, { logout })(Navbar);
