import React, { useState } from "react";
import { Link, Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { signup } from "../../actions/auth";
import { FcGoogle } from "react-icons/fc";
import { BsFacebook } from "react-icons/bs";
import formbg from "../../assets/main_templates_bg/tm_bg.png";
import Footer from "../../includes/Footer";
import axios from "axios";

const Signup = ({ signup, isAuthenticated }) => {
  const [accountCreated, setAccountCreated] = useState(false);
  const [formData, setFormData] = useState({
    first_name: "",
    last_name: "",
    email: "",
    password: "",
    re_password: "",
  });

  const { first_name, last_name, email, password, re_password } = formData;

  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = (e) => {
    e.preventDefault();

    if (password === re_password) {
      signup(first_name, last_name, email, password, re_password);
      setAccountCreated(true);
    }
  };

  const continueWithGoogle = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_API_URL}/auth/o/google-oauth2/?redirect_uri=${process.env.REACT_APP_API_URL}/google`
      );

      window.location.replace(res.data.authorization_url);
    } catch (err) {}
  };

  const continueWithFacebook = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_API_URL}/auth/o/facebook/?redirect_uri=${process.env.REACT_APP_API_URL}/facebook`
      );

      window.location.replace(res.data.authorization_url);
    } catch (err) {}
  };

  if (isAuthenticated) {
    return <Redirect to="/" />;
  }
  if (accountCreated) {
    return <Redirect to="/login" />;
  }

  return (
    <>
      <div className="container py-5">
        <div className="row d-flex align-items-center justify-content-center h-100">
          <div className="col col-md-6 col-sm-12 py-5">
            <img src={formbg} className="img-fluid" alt="Phone logo" />
          </div>
          <div className="col col-md-5 col-sm-12">
            <h1 className="text-bold w-100 mb-5 text-center">Kayıt Ol</h1>

            <form onSubmit={(e) => onSubmit(e)}>
              <div className="form-outline text-left">
                <input
                  id="form1Example13"
                  className="form-control"
                  type="text"
                  placeholder="Ad"
                  name="first_name"
                  value={first_name}
                  onChange={(e) => onChange(e)}
                  required
                />
                <label className="form-label" for="form1Example13">
                  Ad
                </label>
              </div>
              <div className="form-outline text-left">
                <input
                  id="form1Example23"
                  className="form-control"
                  type="text"
                  placeholder="Soyad"
                  name="last_name"
                  value={last_name}
                  onChange={(e) => onChange(e)}
                  required
                />
                <label className="form-label" for="form1Example23">
                  Soyad
                </label>
              </div>
              <div className="form-outline text-left">
                <input
                  id="form1Example33"
                  className="form-control"
                  type="email"
                  placeholder="E-posta adersi"
                  name="email"
                  value={email}
                  onChange={(e) => onChange(e)}
                  required
                />
                <label className="form-label" for="form1Example33">
                  E-posta adresi
                </label>
              </div>
              <div className="form-outline text-left">
                <input
                  id="form1Example43"
                  className="form-control"
                  type="password"
                  placeholder="Şifre"
                  name="password"
                  value={password}
                  onChange={(e) => onChange(e)}
                  minLength="6"
                  required
                />
                <label className="form-label" for="form1Example43">
                  Şifre
                </label>
              </div>
              <div className="form-outline text-left">
                <input
                  id="form1Example53"
                  className="form-control"
                  type="password"
                  placeholder="Şifre Kontrolü"
                  name="re_password"
                  value={re_password}
                  onChange={(e) => onChange(e)}
                  minLength="6"
                  required
                />
                <label className="form-label" for="form1Example53">
                  Şifre Kontrolü
                </label>
              </div>

              <button type="submit" className="btn btn-danger btn-block">
                Kaydet
              </button>
            </form>
            <p className="text-left mx-auto mb-0 mt-3 text-muted">
              Temiz hesabım var?
              <Link to="/login" className="text-dark ms-3">
                giriş yap
              </Link>
            </p>

            <div className="divider d-flex align-items-center my-3">
              <p className="text-center fw-bold mx-auto mb-0 text-muted">OR</p>
            </div>
            <button
              className="btn btn-primary google btn-block"
              onClick={continueWithGoogle}
            >
              <i className="me-4">
                <FcGoogle />
              </i>
              Google ile devam et
            </button>
            <button
              className="btn btn-primary btn-block"
              onClick={continueWithFacebook}
            >
              <i className="me-4">
                <BsFacebook />
              </i>
              Facebook ile devam et
            </button>
            <p className="text-center mx-3 mt-4 text-muted text-2">
              Devam ederek Temiz'in Hizmet Şartlarını ve{" "}
              <strong>Gizlilik Politikamızı </strong>
              okuduğunuzu onaylayın
            </p>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps, { signup })(Signup);
